import React, {Component} from 'react';
import './Header.css'

import {
  FacebookShareButton,
  FacebookIcon,
  TwitterShareButton,
  TwitterIcon
} from 'react-share';


class Header extends Component {
  constructor(props) {
    super(props)
    this.logo = props.logo

  }


  render() {
    return (
      <header className="navbar-fixed-top">
          <div className="container">
              <div className="row">
                <div className="logo"><img src={this.logo} /></div>
              </div>
          </div>
      </header>
    )
  }
}


export default Header
