import React, { Component } from 'react';
import { HashRouter as Router, Route, Switch, Redirect } from 'react-router-dom'
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css'
import BootstrapSlider from 'bootstrap-slider/dist/css/bootstrap-slider.min.css';
import Header from './components/layout/Header'
import Landing from './components/window/Landing'
import Question from './components/window/Question'
import NotFound from './components/window/NotFound'
import About from './components/window/About'
import Results from './components/window/Results'
import Intro from './components/window/Intro'
import { Provider } from './context'
import logo from './logo.png'

class App extends Component {
  render() {
    return (
      <Provider>
          <Router>
            <div className="App">
            <Header logo={logo} />
            <Switch>
                <Route exact={true} path='/' component={Landing} />
                <Route exact={true} path='/intro' component={Intro} />
                <Route exact={true} path='/:country' component={Question} />
                <Route exact={true} path='/charts/:country' component={Results} />
                <Route component={NotFound} />
            </Switch>
        </div>
        </Router>
      </Provider>
    );
  }
}

export default App;
