import React, { Component } from 'react'
import { Consumer } from '../../context'
import './Intro.css'
let ReactRotatingText = require('react-rotating-text');

class Intro extends Component {
  constructor(props) {
    super(props);
    this.state = {
      'country': '',
      'currency': 'usd',
      'error': ''
    }

    this.handleCountry = this.handleCountry.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleCurrency = this.handleCurrency.bind(this)
  }

  handleCountry(event) {
    this.setState({country: event.target.value});
  }

  handleCurrency(event) {
    this.setState({currency: event.target.value});
  }

  handleSubmit(event) {
    const { country, currency } = this.state
    if (country === '') {
      event.preventDefault()
      this.setState({error: 'Country field is required'})
      return;
    } else {
      this.props.history.push('/' + country + '?currency=' + currency)
    }
  }

  render() {
    return (
      <Consumer>
          {value => {
              const {countries} = value[0]
              const _countries = Object.values(countries)
              let options = [{
                'value': '----',
                'label': '----'
              }]

              for (let i = 0; i < _countries.length; i++) {
                options.push({
                  value: _countries[i].country,
                  label: _countries[i].country
                })
              }

              return (
                <React.Fragment>
                  <div className="container intro">
                      <div className="row">
                        <h2><span className='black'>Do you know</span><br /><span className='black'>the price of</span><br /><span id='flipper'><ReactRotatingText items={['milk', 'A meal at an inexpensive restaurant', 'McDonalds meal', 'Coke', 'white bread']} /></span></h2>
                        <form onSubmit={this.handleSubmit}>
                            <label>
                              <select className="select-style" value={this.state.country} onChange={this.handleCountry}>
                                {options.map((x, i) =>
                                  <option value={x.label}>{x.value}</option>
                                )}
                              </select>
                              <div className="invalid-feedback">{this.state.error}</div>
                            </label>
                            <br />
                            <p className="form-text text-muted">SELECT THE CURRENCY</p>
                            <div className="radio">
                              <label>
                                <input type="radio" value="usd"
                                              checked={this.state.currency === 'usd'}
                                              onChange={this.handleCurrency} />
                                            US Dollar
                              </label>
                            </div>
                            <div className="radio">
                              <label>
                                <input type="radio" value="local"
                                              checked={this.state.currency === 'local'}
                                              onChange={this.handleCurrency} />
                                            Local currency
                              </label>
                            </div>
                            <br />
                            <input className="btn btn-blue" type="submit" value="Next" />
                        </form>
                      </div>
                  </div>

                </React.Fragment>
              )
          }}
      </Consumer>
    )
  }
}


export default Intro;
