import React, { Component } from 'react'
import './Results.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import { Carousel } from 'react-bootstrap';

class Results extends Component {
  constructor(props) {
    super()
    let _country = props.location.pathname.split('/')
    this.state = {
      'country': _country[2]
    }
  }


  createImages = () => {
    let images = []
    let country = this.state.country
    let final = []
    for (let i = 1; i < 26; i++) {
      let path = "/charts/" + country + "/" + country + "_q" + i + ".png"
      images.push(<Carousel.Item> <img width={900} height={500} alt="900x500" src={path} /></Carousel.Item>)
    }
    return images
  }

  render () {
    return (
      <React.Fragment>
        <div className="container intro">
          <div className="row">
            <h2>Where does {this.state.country} rank</h2>
            <br />
              <Carousel>
                {this.createImages()}
              </Carousel>
          </div>
        </div>
      </React.Fragment>
    )
  }
}

export default Results;
